<template>
<div class="fx-result">
    <!-- <header-text :headerText="headerTitle" @header-event="onHeaderBtn"></header-text> -->
    <div v-show="flag == 0" class="rejectBg bg"></div>
    <div v-show="flag == 1 || flag == 2" class="passBg bg"></div>
    <div v-show="flag == 3 || flag == 4" class="warnBg bg"></div>
    <div class="title">{{result.resultTitle}}</div>
    <div class="title1">{{result.resultTitle1}}</div>
    <div class="content">{{result.resultText}}<span class="linktext" v-show="flag == 2" v-html="result.resultLink"></span></div>
    <div class="list-item" v-show="flag == 4 && result.resultLink" v-html="result.resultLink"></div>
    <div class="content">{{result.resultChose}}</div>
    <div class="btns">
        <div v-show="flag == 1" class="oneBtn blueBtn" @click="resultAction(0)">继续投保</div>
        <div v-show="flag == 0">
            <div  class="oneBtn blueBtn" @click="resultAction(1)">联系保险顾问</div>
            <div  class="oneBtn whiteBtn" @click="resultAction(2)">返回</div>
        </div>
        <div v-show="flag == 2">
            <div  class="twoBtn whiteBtn1" @click="resultAction(3)">取消投保</div>
            <div  class="twoBtn blueBtn" @click="resultAction(4)">继续投保</div>
        </div>
        <div v-show="flag == 3">
            <div  class="oneBtn blueBtn" @click="resultAction(1)">联系在线客服</div>
        </div>
        <div v-show="flag == 4">
            <div  class="twoBtn whiteBtn1" @click="resultAction(3)">取消投保</div>
            <div  class="twoBtn blueBtn" @click="resultAction(5)">转人工核保</div>
        </div>
    </div>
</div>
</template>
    
<script>
import Vue from "vue";
import headerText from "@/components/header";
export default {
    name:'result',
    data(){
        return{
            headerTitle:'核保结果',
            flag:null,
            json:{//100 通过 101 拒保 102 除责 103 人工核保 201 数据不正确
            "1":{
                resultType:1,//0为未通过 1为通过 2为带条件投保 3为补充资料
                resultTitle:"您已通过审核，可以正常投保",
                resultText:"您的健康报告已通过本次审核，恭喜您可以正常投保本产品。"
            },
            "0":{
                resultType:0,//0为未通过 1为通过 2为带条件投保 3为补充资料
                resultTitle:"抱歉未通过审核",
                resultText:"您的健康告知不符合保险公司的审核标准，可联系梧桐树保险顾问为您推荐其他产品。"
            },
            "3":{
                resultType:3,//0为未通过 1为通过 2为带条件投保 3为补充资料
                resultTitle:"sorry,系统走神啦，请联系",
                resultTitle1:'在线客服帮您解决'
            },
            "2":{
                resultType:2,//0为未通过 1为通过 2为带条件投保 3为补充资料
                resultTitle:"需除外部分责任后投保",
                resultText:"您本次核保的结论是：",
                resultLink:"",//errorContent
                resultChose:"您是否接受此条件继续投保？"
            },
            "4":{
                resultType:4,//0为未通过 1为通过 2为带条件投保 3为补充资料
                resultTitle:"转人工核保",
                resultText:"根据您的健康告知，需要转人工核保进行审核，人工核保需要1-3个工作日，您是否需要转人工核保？",
                resultLink:"",//errorContent
            }
            },
            result:{}
        }
    },
    created(){

    },
    methods:{
        //点击header
        onHeaderBtn(headerText){
            console.log(headerText);
        },
        //根据id找到最后一级病种
        findItemById(arr,targetId){
            //判断type是否等于1 ===》病种
            var targetItem;
            let findItem = function(array,itemId){
                array.forEach((item) => {
                    if(item.id == itemId){
                        //console.log("item",item);
                        if(item.type === 1 || item.type === 4){
                            targetItem = item;
                        }
                        else{
                            console.log("item",item);
                            findItem(arr,item.parentId);
                        }
                    }
                    else if(item.children.length>0){
                        findItem(item.children,itemId);
                    }
                });
            }
            let tmp1 = findItem(arr,targetId);
            console.log("targetItem",targetItem);
            return targetItem.content
        },
        //请求结论页面
        getResultPage(){
            let THAT = this;
            let errorContent = sessionStorage.getItem("errorContent");
            if(errorContent){
                let tmp = JSON.parse(errorContent);
                //需要加上病种
                let QUES_ILLS = JSON.parse(sessionStorage.getItem("QUES_ILLS"));
                var tmpStr;
                if(tmp.length>1){
                    if(THAT.flag == 2){
                        tmpStr = "<br/>";
                    }
                    else{
                        tmpStr = "";
                    }
                    tmp.map((item,index)=>{
                        if(item.status === 102){
                            let illName = THAT.findItemById(QUES_ILLS,item.diseaseId);
                            console.log("illName",illName);
                            tmpStr = tmpStr +'<span style="color:#666;">'+(index+1) + '.' +illName+'：</span>'+'<br/>'+item.errorContent+'<br/>'
                        }
                    })
                }
                else{
                    tmpStr = "";
                    tmp.map((item,index)=>{
                        if(item.status === 102){
                            let illName = THAT.findItemById(QUES_ILLS,item.diseaseId);
                            console.log("illName",illName);
                            tmpStr = tmpStr +'<span style="color:#666;">'+illName+'：</span>'+'<br/>'+item.errorContent+'<br/>'
                        }
                    })
                }
                THAT.json["2"].resultLink = tmpStr;
                if(tmpStr !== ""){
                    THAT.json["4"].resultLink = '<div class="list-title">除外项</div>'+tmpStr;
                }
                else{
                    THAT.json["4"].resultLink = null;
                }
            }
            console.log(THAT.$route.query.resultType);
            let flag = THAT.$route.query.resultType;
            THAT.result = THAT.json[flag];
            THAT.flag = flag;
        },
        //点击按钮事件
        resultAction(type){
            console.log("this.$common.api_root",this.$common.api_root);
            console.log(type);
            let HEALTHY_TYPE = sessionStorage.getItem("HEALTHY_TYPE");//0 投保人 1被保人
            let JUMP_KEY = sessionStorage.getItem("JUMP_KEY");
            let proCode = sessionStorage.getItem("proCode");
            console.log("HEALTHY_TYPE",HEALTHY_TYPE);
            if(type === 3){//取消投保
                if(HEALTHY_TYPE === "1"){//已经是被投保人健康告知了
                    //this.$router.push({path:'/fuxing/healthy'});
                    window.location.href = this.$common.api_root+"/market/healthy.html?key="+JUMP_KEY+"&proCode=" + proCode;
                }
                else if(HEALTHY_TYPE === "0"){
                    window.location.href = this.$common.api_root+"/market/healthy-holder.html?key="+JUMP_KEY+"&proCode=" + proCode;
                }
            }
            if(type === 5){//人工核保 //调订单页
                window.location.href = this.$common.api_root+"/market/writeInform-3.html?key="+JUMP_KEY+"&proCode=" + proCode;
                //这里后台处理
                //this.$router.push({path:'/fuxing/questionnaire'})
            }
            if(type === 0){//继续投保
                console.log("跳到订单页");
                if(HEALTHY_TYPE === "0"){//跳到被保人健康告知
                    window.location.href = this.$common.api_root+"/market/healthy.html?key="+JUMP_KEY+"&proCode=" + proCode;
                }
                else if(HEALTHY_TYPE === "1"){
                    window.location.href = this.$common.api_root+"/market/writeInform-3.html?key="+JUMP_KEY+"&proCode=" + proCode;
                }
                //this.$common.api_root
            }
            if(type === 1){
                console.log("客服页面");
                window.location.href = 'https://bx.wts999.com/m/customer/index.html';
                //https://bx.wts999.com/m/customer/index.html
            }
            if(type === 2){//返回
                // this.$router.push({path:'/fuxing/healthy'})
                if(HEALTHY_TYPE === "1"){//已经是被投保人健康告知了
                    //this.$router.push({path:'/fuxing/healthy'});
                    window.location.href = this.$common.api_root+"/market/healthy.html?key="+JUMP_KEY+"&proCode=" + proCode;
                }
                else if(HEALTHY_TYPE === "0"){
                    window.location.href = this.$common.api_root+"/market/healthy-holder.html?key="+JUMP_KEY+"&proCode=" + proCode;
                }
            }
            if(type === 4){
                // console.log('aa');
                if(HEALTHY_TYPE === "1"){//已经是被投保人健康告知了
                    window.location.href = this.$common.api_root+"/market/writeInform-3.html?key="+JUMP_KEY+"&proCode=" + proCode;
                    //this.$router.push({path:'/fuxing/healthy'});
                    //window.location.href = this.$common.api_root+"/market/healthy.html?key="+JUMP_KEY+"&proCode=" + proCode;
                }
                else if(HEALTHY_TYPE === "0"){
                    window.location.href = this.$common.api_root+"/market/healthy-holder.html?key="+JUMP_KEY+"&proCode=" + proCode;
                }
            }
        }
    },
    computed:{},
    components:{
        "header-text":headerText
    },
    mounted(){
        this.getResultPage();
    }
}
</script>

<style lang="scss">
.fx-result{
    width:100%;
    height:100%;
    background:#fff;
    position: relative;
    overflow-y: auto;
    .rejectBg{
        background: url("../../../static/images/rejectbg.png") no-repeat;
    }
    .passBg{
        background: url("../../../static/images/passbg.png") no-repeat;
    }
    .warnBg{
        background: url("../../../static/images/pro.png") no-repeat;
    }
    .bg{
        width:r(519);
        height:r(490);
        background-size:100% 100%;
        margin:r(120) auto 0;
    }
    .title{
        font-size:r(34);
        color:#353535;
        margin-top:r(-129);
        text-align: center;
        margin-bottom:r(10);
    }
    .title1{
        font-size:r(34);
        color:#353535;
        text-align: center;
    }
    .content{
        line-height:r(46);
        padding:0 r(80);
        color:#666;
        font-size:r(26);
        .linktext{
            color:#0184ff;
        }
    }
    .list-item{
        line-height:r(46);
        margin:0 auto;
        margin-top:r(20);
        color:#0184ff;
        font-size:r(28);
        background:#f6f6f6;
        width:r(666);
        padding:r(20) r(26);
        border-radius:r(10);
        .list-title{
            font-size:r(28);
            color:#46a0fe;
            font-weight:bold;
        }
    }
    .btns{
        padding:r(80) r(30) 0 r(30);
        overflow: hidden;
        margin-bottom:r(40);
        .oneBtn{
            height:r(88);
            line-height:r(88);
            font-size:r(32);
            text-align: center;
            border-radius:r(10);
            margin-bottom:r(40);
        }
        .twoBtn{
            height:r(88);
            line-height:r(88);
            font-size:r(32);
            text-align: center;
            border-radius:r(10);
            width:r(330);
            float: right;
        }
        .blueBtn{
            background:#0084ff;
            color:#fff;
        }
        .whiteBtn{
            color:#000;
            background:url("../../../static/images/btnbg.png") no-repeat;
            background-size:100% 100%;
            line-height:r(107);
            width:r(709);
            height:r(107);
            margin-top:r(-9);
        }
        .whiteBtn1{
            color:#000;
            background:url("../../../static/images/btnbg1.png") no-repeat;
            background-size:100% 100%;
            line-height:r(107);
            width:r(349);
            height:r(107);
            margin-top:r(-9);
            float:left;
        }
    }
}
</style>


